<template>
    <div class="github">
      <div class="github__container">
          <div class="github__contentcontainer">
              <div class="github__avatarhovercontainer">
                <div class="github__avatarhover--1" :style="githubAvatar()"></div>
                <div class="github__avatarhover--2" :style="githubAvatar()"></div>
              </div>
              <div class="github__avatar" :style="githubAvatar()"></div>
          </div>
      </div>
      <div class="github__container">
        <div class="github__contentcontainer">
          <div>
            <div class="github__titlecontainer">
              <img src="../assets/icons/github.svg" class="col github__icon"/>
              <h3 class="github__title">Github stats</h3>
            </div>
            <div class="row github__stats">
              <div class="col-sm-6"><b>Username:</b></div>
              <div class="col-sm-6">{{ getGithubStats.name }}
                <p v-show="loading">Loading...</p>
              </div>
            </div>
            <div class="row github__stats">
              <div class="col-sm-6"><b>Location:</b></div>
              <div class="col-sm-6">{{ getGithubStats.location }}
                <p v-show="loading">Loading...</p>
              </div>
            </div>
            <div class="row github__stats">
              <div class="col-sm-6"><b>Company:</b></div>
              <div class="col-sm-6">{{ getGithubStats.company }}
                <p v-show="loading">Loading...</p>
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex';

export default {
    data() {
        return{
            loading: false,
        }
    },
    methods: {
        loadGithub: function() {
            return this.$store.dispatch('asyncChangeGithub').then(() => {
            });
        },
        githubAvatar() {
            return {
                backgroundImage: "url('" + this.getGithubStats.avatar + "')"
            };
        }
    },
    computed: {
        ...mapGetters([
            'getGithubStats',
        ]),
    },
    mounted: function() {
        this.loading = true;

        this.loadGithub().then(() =>  {
            this.loading = false;
        }).catch(function (error) {
            this.loading = false;
        })
    }
}
</script>



