import Vue from 'vue'
import Router from 'vue-router'
import Main from '../layouts/MainLayout'
import Contact from '../layouts/ContactLayout'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      components: {
        default: Main
      }
    },
    {
      path: '/contact',
      name: 'Contact',
      components: {default: Contact}
    },
      {
      path: '*',
      components: {
          default: Main,
      }
      }
  ],
    mode: 'history'
})



