import axios from 'axios'
import { getGithubdata } from '../../helpers/api-helper'

const state = {
    githubStats: {
        name: "",
        avatar: "",
        company: "",
        location: ""
    },
};

const getters = {
    getGithubStats(state) {
        return state.githubStats;
    }
};

const mutations = {
    changeGithub: (state, response) => {
            if(response instanceof Error == true) {
                console.log(response);
                return;
            }
            state.githubStats.name = response["data"]["login"];
            state.githubStats.avatar = response["data"]["avatar_url"];
            state.githubStats.company = response["data"]["company"];
            state.githubStats.location = response["data"]["location"];
        }
};

const actions = {
    asyncChangeGithub({ commit }) {
            return axios.get('https://api.github.com/users/dylangonzalezz').then(function(response) {
                commit('changeGithub', response);
            }).catch(function (error) {
                commit('changeGithub', error);
        });
    }
};

export default {
    state,
    getters,
    mutations,
    actions
};



